import React, { useState } from "react";
import { Input, Card, Button, Row, Col, Badge } from "antd";
import { ShoppingCartOutlined } from "@ant-design/icons";

// const { Search } = Input;

const menuData = [
  { id: 1, name: "Lu ch Mng Chua", price: 89000, image: "/img/lauechmang.png" },
  { id: 2, name: "Lu C H", price: 79000, image: "/img/laucahu.png" },
  { id: 3, name: "Lu u C Hi", price: 79000, image: "/img/laucahoi.png" },
  { id: 4, name: "Lu Tm Cng Xanh", price: 99000, image: "/img/tomcang.png" },
  { id: 5, name: "Lu Thi Hi Sn", price: 89000, image: "/img/lauthai.png" },
  { id: 6, name: "Lu Nm Chay", price: 79000, image: "/img/lauchay.png" },
  { id: 7, name: "Lu G L Giang", price: 99000, image: "/img/laulagiang.png" },
  { id: 8, name: "Lu B Nhng Dm", price: 109000, image: "/img/launhunggiam.png" },
  { id: 9, name: "Lu C Lng", price: 95000, image: "/img/laucalang.png" },
  { id: 10, name: "Lu Tm S", price: 119000, image: "/img/lautomsu.png" },
  { id: 11, name: "Lu Sn Non", price: 89000, image: "/img/lausuonnon.png" },
  { id: 12, name: "Lu G t Him", price: 99000, image: "/img/laugaothiem.png" },
];

function MenuPage() {
  const [cart, setCart] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");

  const addToCart = (item) => {
    setCart([...cart, item]);
  };

  const filteredMenu = menuData.filter((item) =>
    item.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div style={{ padding: "20px" }}>
      {/* Header */}
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <h1>Thc n</h1>
        <Badge count={cart.length} showZero>
          <Button
            type="primary"
            icon={<ShoppingCartOutlined />}
            onClick={() => (window.location.href = "/cart")}
          >
            Gi Hng
          </Button>
        </Badge>
      </div>

      {/* Search */}
      {/* <Search
        placeholder="Tm mn n"
        onSearch={(value) => setSearchTerm(value)}
        style={{ width: "100%", marginBottom: "20px" }}
      /> */}

      {/* Menu Items */}
      <Row gutter={[16, 16]}>
        {filteredMenu.map((item) => (
          <Col xs={24} sm={12} md={8} lg={6} key={item.id}>
            <Card
              hoverable
              cover={
                <div
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "150px",
                    overflow: "hidden",
                  }}
                >
                  <img
                    alt={item.name}
                    src={item.image}
                    style={{
                      width: "100%",
                      height: "auto",
                      maxHeight: "150px",
                      objectFit: "cover",
                    }}
                  />
                </div>
              }
              actions={[
                <Button type="primary" onClick={() => addToCart(item)}>
                  Thm vo gi
                </Button>,
              ]}
            >
              <Card.Meta title={item.name} description={`${item.price.toLocaleString()} `} />
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default MenuPage;
